import React, { Component } from 'react';
import CatBig from '../layout/loaders/CatBig';

class Gallery extends Component {

    render() {
        const { images } = this.props;
        if(!(Object.entries(images).length === 0 && images.constructor === Object)){
            return (
                <div className="gallery-container">
                    {Object.values(images).map(unit => {
                        let sizes = this.fixSize(unit);
                        return <img className="gallery-item" key={unit.id} src={unit.url} alt="Cat" height={sizes[0]} width={sizes[1]}/>
                    })}
                </div>
            )
        }else{
            return <CatBig/>
        }
    }

    fixSize = (unit) => {
        if(unit.height < 800){
            return [unit.height/4, unit.width/4];
        }else if(unit.height < 1000){
            return [unit.height/5, unit.width/5];;
        }else if(unit.height < 1200){
            return [unit.height/6, unit.width/6];;
        }else{
            return [unit.height/8, unit.width/8];;
        }
    }
}   

export default Gallery;